import React, { Component } from "react";
import { createPortal } from 'react-dom';
import PropTypes from "prop-types";
import uniq from "lodash/uniq";
import get from "lodash/get";
import merge from "lodash/merge";
import debounce from "lodash/debounce";
import isEmpty from "lodash/isEmpty";
import Select from 'react-select';
import Masonry from "react-masonry-component";
import TextTransition from "react-text-transition";
import ReactPlayer from "react-player";
import ReactLoading from "react-loading";
import {
    FacebookShareButton,
    FacebookIcon,
    EmailShareButton,
    EmailIcon,
    TwitterShareButton,
    TwitterIcon,
    LinkedinShareButton,
    LinkedinIcon
} from "react-share";
import Img from "react-image";
import BestPrice from "@sabre/shs-widgets-best-price";
import defaultLabels from "./labels";
import "./gallery.scss";

class GalleryComponent extends Component {
    state = {
        images: this.props.images,
        categories: this.getCategories(this.props.images),
        category: this.props.category,
        activeImage: this.props.activeImage || -1,
        activeVariant: 0,
        width: window.innerWidth
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.state.activeImage === -1 && prevState.activeImage > -1) {
            document.body.classList.remove('shs-widgets-gallery-modal-open');
        }
        else if (this.state.activeImage > -1 && prevState.activeImage === -1) {
            document.body.classList.add('shs-widgets-gallery-modal-open');
        }
    }

    componentDidMount() {
        this.debouncedResize = debounce(this.handleResize, 200);
        window.addEventListener("keyup", this.keyHandling);
        window.addEventListener("resize", this.debouncedResize);
    }

    componentWillUnmount() {
        window.removeEventListener("keyup", this.keyHandling);
        window.removeEventListener("resize", this.debouncedResize);
    }

    render() {
        const { categories, category, images } = this.state;
        const labels = merge({}, defaultLabels, this.props.labels);

        if (!this.props.api) {
            return <div>Missing API key.</div>;
        }

        return (
            <div className="shs-widgets-gallery">
                <nav className="shs-widgets-gallery--categories">
                    {
                        this.isMobile() && (
                            <Select className="category-select" classNamePrefix="category-nav" value={{value: category, label: category === 'all' ? labels.categoriesAll : category}} options={[
                                    {
                                        value: 'all',
                                        label: labels.categoriesAll
                                    },
                                    ...categories.map((cat) => ({value: cat, label: cat}))
                                ]}
                                onChange={(category) => this.setCategory(category.value)}
                            />
                        )
                    }
                    {
                        !this.isMobile() && (
                            <ul>
                                <li>
                                    <button
                                        className={`${
                                            category === "all" ? "active" : ""
                                            }`}
                                        onClick={() => this.setCategory("all")}
                                    >
                                        {labels.categoriesAll}
                                    </button>
                                </li>
                                {categories.map(cat => {
                                    return (
                                        <li key={`category-nav-${cat}`}>
                                            <button
                                                className={`${
                                                    cat === category ? "active" : ""
                                                    }`}
                                                onClick={() => this.setCategory(cat)}
                                            >
                                                {cat}
                                            </button>
                                        </li>
                                    );
                                })}
                            </ul>
                        )
                }
                </nav>
                <Masonry
                    className="gallery-container"
                    options={{
                        fitWidth: true,
                        gutter: this.props.gutter
                    }}
                >
                    {images.map((img, index) => {
                        return (
                            <div
                                onClick={() => this.setActiveImage(index)}
                                key={`block-image-${img.img}-${index}`}
                                className="shs-widgets-gallery--item"
                            >
                                <div className="shs-widgets-gallery--item__container">
                                    <img
                                        style={{
                                            width: `${this.props.imageWidth}px`,
                                            marginBottom: `${
                                                this.props.gutter
                                            }px`
                                        }}
                                        src={img.img}
                                        alt={img.alt}
                                    />
                                    {img.video && (
                                        <div className="video-icon" />
                                    )}
                                </div>
                            </div>
                        );
                    })}
                </Masonry>
                {this.state.activeImage > -1 && this.renderDetail(labels)}
            </div>
        );
    }

    keyHandling = event => {
        if (this.state.activeImage > -1) {
            if (event.keyCode === 39) {
                this.goForward();
            }

            if (event.keyCode === 37) {
                this.goBack();
            }
        }
    };

    handleResize = event => {
        this.setState({
            width: window.innerWidth
        });
    };

    renderDetail = labels => {
        const {
            socialShareHashTag,
            socialShareTitle,
            socialShareUrl,
            socialShareBody
        } = this.props;
        let activeObject = null;

        const imageObject = get(
            this.state.images,
            `[${this.state.activeImage}]`
        );

        if (!imageObject) {
            return null;
        }

        if (isEmpty(imageObject.variants)) {
            activeObject = imageObject;
        } else {
            activeObject = merge(
                {},
                imageObject,
                imageObject.variants[this.state.activeVariant]
            );
        }

        const {
            title,
            subTitle,
            description,
            imgFull,
            socialConfig,
            video
        } = activeObject;
        const socialConfigMerge = merge(
            {},
            {
                socialShareHashTag,
                socialShareTitle,
                socialShareUrl,
                socialShareBody
            },
            socialConfig
        );

        return createPortal(
            <div className="shs-widgets-gallery--overlay">
                <div className="shs-widgets-gallery--modal">
                    <div className="detail-image">
                        {video ? (
                            <ReactPlayer url={video} playing controls />
                        ) : (
                            <Img
                                src={imgFull}
                                loader={
                                    <div className="image-loading">
                                        <ReactLoading
                                            type="spin"
                                            color="#fff"
                                            height={40}
                                            width={40}
                                        />
                                    </div>
                                }
                            />
                        )}
                    </div>
                    <div className="detail-info">
                        <button className="close" onClick={this.closeModal}>
                            ×
                        </button>
                        {subTitle && (
                            <div className="sub-title">
                                <TextTransition
                                    text={subTitle}
                                    spring={{ stiffness: 120, damping: 17 }}
                                />
                            </div>
                        )}
                        {title && (
                            <div className="title">
                                <TextTransition
                                    text={title}
                                    spring={{ stiffness: 120, damping: 17 }}
                                />
                            </div>
                        )}
                        <div
                            className="description"
                            dangerouslySetInnerHTML={{ __html: description }}
                        />
                        {!isEmpty(imageObject.variants) &&
                            this.renderVariantsControls(imageObject)}
                        {this.renderControls(activeObject)}
                        {socialConfigMerge.socialShareUrl && (
                            <div className="social-share">
                                <EmailShareButton
                                    subject={socialConfigMerge.socialShareTitle}
                                    body={socialConfigMerge.socialShareBody}
                                    url={socialConfigMerge.socialShareUrl}
                                >
                                    <EmailIcon size={28} />
                                    <span className="share-text">
                                        {labels.socialShare}
                                    </span>
                                </EmailShareButton>
                                <TwitterShareButton
                                    url={socialConfigMerge.socialShareUrl}
                                    title={socialConfigMerge.socialShareTitle}
                                    hashtag={
                                        socialConfigMerge.socialShareHashTag
                                    }
                                >
                                    <TwitterIcon size={28} />
                                    <span className="share-text">
                                        {labels.socialTweet}
                                    </span>
                                </TwitterShareButton>
                                <FacebookShareButton
                                    url={socialConfigMerge.socialShareUrl}
                                    quote={socialConfigMerge.socialShareTitle}
                                >
                                    <FacebookIcon size={28} />
                                    <span className="share-text">
                                        {labels.socialLikeIt}
                                    </span>
                                </FacebookShareButton>
                                <LinkedinShareButton
                                    url={socialConfigMerge.socialShareUrl}
                                >
                                    <LinkedinIcon size={28} />
                                    <span className="share-text">
                                        {labels.socialLinkIt}
                                    </span>
                                </LinkedinShareButton>
                            </div>
                        )}
                        <div className="nav-buttons">
                            <button
                                className="prev"
                                onClick={this.goBack}
                                title={labels.previous}
                            >
                                <i className="arrow left" />
                            </button>
                            <button
                                className="next"
                                onClick={this.goForward}
                                title={labels.next}
                            >
                                <i className="arrow right" />
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        , document.body);
    };

    renderVariantsControls = obj => {
        return obj.variants.map((variant, index) => {
            return (
                <span className="radio-group">
                    <input
                        onChange={() => this.setState({ activeVariant: index })}
                        checked={this.state.activeVariant === index}
                        type="radio"
                        id={`radio-id-${index}`}
                        name="gallery-variants"
                    />
                    <label htmlFor={`radio-id-${index}`}>{variant.label}</label>
                </span>
            );
        });
    };

    renderControls = data => {
        if (!data.controls) {
            return null;
        }

        return (
            <div className="controls">
                {data.controls.map(control => {
                    if (control.type === "button") {
                        return (
                            <div className="control-item">
                                <button
                                    style={control.style}
                                    className={`control-button ${
                                        control.className
                                    }`}
                                    onClick={() => {
                                        if (this.props.isWebComponent) {
                                            return (
                                                window[control.onClick] &&
                                                window[control.onClick](data)
                                            );
                                        }

                                        return (
                                            control.onClick &&
                                            control.onClick(data)
                                        );
                                    }}
                                >
                                    {control.label}
                                </button>
                            </div>
                        );
                    }

                    if (control.type === "price") {
                        return (
                            <div className="control-item">
                                <BestPrice
                                    apiWidget="widgets-gallery"
                                    api={this.props.api}
                                    {...control.props}
                                />
                            </div>
                        );
                    }

                    return null;
                })}
            </div>
        );
    };

    goForward = () => {
        const { activeImage, images } = this.state;
        if (activeImage === images.length - 1) {
            return this.setState({
                activeImage: 0
            });
        }

        return this.setState({
            activeImage: activeImage + 1
        });
    };

    goBack = () => {
        const { activeImage, images } = this.state;
        if (activeImage === 0) {
            return this.setState({
                activeImage: images.length - 1
            });
        }

        return this.setState({
            activeImage: activeImage - 1
        });
    };

    setActiveImage = index => {
        this.setState({
            activeImage: index
        });
    };

    closeModal = () => {
        this.setState({
            activeImage: -1
        });
    };

    setCategory = code => {
        return this.setState(
            {
                category: code
            },
            () => this.filterResults()
        );
    };

    filterResults = () => {
        const { category } = this.state;

        if (category === "all") {
            return this.setState({
                images: this.props.images
            });
        }

        return this.setState({
            images: this.props.images.filter(img => img.category === category)
        });
    };

    isMobile() {
        return this.state.width < 768;
    }

    getCategories(images) {
        return uniq(images.map(img => img.category));
    }
}

GalleryComponent.propTypes = {
    api: PropTypes.string.isRequired,
    isWebComponent: PropTypes.bool,
    labels: PropTypes.object,
    activeImage: PropTypes.number,
    imageWidth: PropTypes.number,
    gutter: PropTypes.number,
    category: PropTypes.string,
    images: PropTypes.arrayOf(
        PropTypes.shape({
            img: PropTypes.string.isRequired,
            imgFull: PropTypes.string.isRequired,
            video: PropTypes.string,
            title: PropTypes.string,
            subTitle: PropTypes.string,
            description: PropTypes.string,
            category: PropTypes.string,
            socialConfig: PropTypes.shape({
                socialShareHashTag: PropTypes.array,
                socialShareTitle: PropTypes.string,
                socialShareUrl: PropTypes.string,
                socialShareBody: PropTypes.string
            }),
            variants: PropTypes.arrayOf(
                PropTypes.shape({
                    label: PropTypes.string.isRequired,
                    imgFull: PropTypes.string.isRequired,
                    video: PropTypes.string,
                    title: PropTypes.string,
                    subTitle: PropTypes.string,
                    description: PropTypes.string,
                    socialConfig: PropTypes.shape({
                        socialShareHashTag: PropTypes.array,
                        socialShareTitle: PropTypes.string,
                        socialShareUrl: PropTypes.string,
                        socialShareBody: PropTypes.string
                    }),
                    controls: PropTypes.arrayOf(
                        PropTypes.shape({
                            type: PropTypes.oneOf(["button", "price"]),
                            onClick: PropTypes.oneOfType([
                                PropTypes.string,
                                PropTypes.func
                            ]),
                            label: PropTypes.string,
                            className: PropTypes.string,
                            style: PropTypes.object,
                            props: PropTypes.object
                        })
                    )
                })
            ),
            controls: PropTypes.arrayOf(
                PropTypes.shape({
                    type: PropTypes.oneOf(["button", "price"]),
                    onClick: PropTypes.oneOfType([
                        PropTypes.string,
                        PropTypes.func
                    ]),
                    // if button it will be button label
                    label: PropTypes.string,
                    className: PropTypes.string,
                    style: PropTypes.object,
                    // if price it requires best price widget props i.e. {"type":"price","props":{"hotel":"10155","chain":"12011","room":"DLXK","labelSlogan":"Rates from","labelSlogan2":""}}
                    props: PropTypes.object
                })
            )
        })
    ).isRequired,
    socialShareUrl: PropTypes.string,
    socialShareTitle: PropTypes.string,
    socialShareHashTag: PropTypes.array,
    socialShareBody: PropTypes.string
};

GalleryComponent.defaultProps = {
    activeImage: -1,
    imageWidth: 280,
    gutter: 20,
    category: "all",
    images: []
};

export default GalleryComponent;
